<template>
  <div id="order-view" class="align-left">
    <div class="space"></div>
    &nbsp;
    <h2>Your orders</h2>

    <b-tabs>
      <template slot="tabs">
        <b-nav-item
          v-for="(item, ind) in tabs"
          v-bind:key="ind"
          :active="activeTab === item.key"
          @click="tabSelected(item.key)"
        >{{item.name}}</b-nav-item>
      </template>
    </b-tabs>

    <div class="order-list">
      <div v-for="(order, oid) in filteredOrders" v-bind:key="oid">
        <single-order :order="order"/>
      </div>
      <strong>Return and Refund Policy</strong><br>
      <br>
      <p>1. Our Marble merchandise is produced with utmost care for product quality. Should there be any manufacturing defect or 
        should the customer want to exchange an unused item fro size, colour, etc., it may be exchanged within 10 days of purchase 
        at any Marble store by producing the cash memo and the item with its original packaging and tags intact <br>
        <br>
       2. Marble does not refund cash or issue cred it notes fore changed items but any item can be purchased in exchange for the same value <br>
        <br>
       3. In the event that there are no store tags or cash memo and in case of soiling/damage to the item in question, no exchange shall be entertained.</p>

      <div v-if="filteredOrders.length <= 0" class="empty-info">
        <p>You haven't placed any orders or haven't logged in.</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import SingleOrder from '@/components/orders/SingleOrder.vue';

export default {
  name: 'OrderView',
  components: {
    SingleOrder,
  },
  data() {
    return {
      tabs: [
        {
          name: 'Orders',
          key: 'all',
        },
        {
          name: 'Open Orders',
          key: 'open',
        },
        {
          name: 'Completed',
          key: 'completed',
        },
        {
          name: 'Cancelled',
          key: 'cancelled',
        },
      ],

      activeTab: 'all',
    };
  },

  async created() {
    // Look for all the items from the order list.
    await this.$store.dispatch('orderStore/getOrderList', '');
  },

  methods: {
    tabSelected(key) {
      this.activeTab = key;
    },
  },

  computed: {
    ...mapGetters({
      orders: 'orderStore/orders',
    }),

    filteredOrders() {
      if (this.activeTab === 'open') {
        return _.filter(this.orders, i => i.overall_status !== 'COMPLETED' && i.overall_status !== 'CANCELLED');
      }
      if (this.activeTab === 'completed') {
        return _.filter(this.orders, i => i.overall_status === 'COMPLETED');
      }
      if (this.activeTab === 'cancelled') {
        return _.filter(this.orders, i => i.overall_status === 'CANCELLED');
      }

      return this.orders;
    },
  },
};
</script>

